import { NgModule }       from '@angular/core';
import { CommonModule }   from '@angular/common';
import { FormsModule }    from '@angular/forms';
import {RouterModule} from "@angular/router";
import {AppManageComponent} from "./app-manage.component";
import {AppManageService} from "./app-manage.service";
import { MaterialModule } from '@angular/material';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import {SharedModule} from '../../shared/shared.module';
import {AppDialog} from './dialogs/app.dialog';
//import {AuthManageComponent} from './auth-manage/auth-manage.component';
// exportshomeexportapp
// LeftBookComponentHomeModuleLeftBookComponent
@NgModule({
  imports: [
    CommonModule,
    NgbModule,
    FormsModule,
    RouterModule,
    MaterialModule,
    SharedModule
  ],
  declarations: [
    AppManageComponent,
    AppDialog
  ],
  entryComponents: [
    AppDialog
  ],
  exports:[
  ],
  providers: [AppManageService]
})
export class AppManageModule {}



