import { NgModule }       from '@angular/core';
import { CommonModule }   from '@angular/common';
import { FormsModule }    from '@angular/forms';
import { MaterialModule } from '@angular/material';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import {RouterModule} from "@angular/router";
import {LogManageComponent} from "./log-manage.component";
import {SharedModule} from "../../shared/shared.module";
import { Daterangepicker } from 'ng2-daterangepicker';
import {LoginLogComponent} from './login-log/login-log.component';
import {OperationLogComponent} from './operation-log/operation-log.component';
import {LogManageService} from './log-manage.service';
//import {AuthManageComponent} from './auth-manage/auth-manage.component';
// exportshomeexportapp
// LeftBookComponentHomeModuleLeftBookComponent
@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    RouterModule,
    SharedModule,
    MaterialModule,
    NgbModule,
    Daterangepicker
  ],
  declarations: [
    LoginLogComponent,
    LogManageComponent,
    OperationLogComponent
  ],
  entryComponents: [

  ],
  exports:[
  ],
  providers: [LogManageService]
})
export class LogManageModule {}



